      // baseURL: `http://shop.robonomics.loc/index.php`
    });
    api.defaults.headers.post["Content-Type"] =
      "application/x-www-form-urlencoded";

    const post = async (params = {}, data = {}) => {
      return api.post("", data, {
        params
      });
    };

    const login = async (force = false) => {
      api_token = localStorage.getItem("api_token_shop");
      if (force || !api_token) {
        const res = await post(
          {
            route: "api/login"
          },
          {
            key: "Pb8XLnxqOsFQrU4zPW9FWQ9CcGpWksY66DuLL3WVIqXSpzBI8vIsVc7h294qZjyee77u2flWR2XZpgjWejKSyr10RSfBMtUW8cYzAvIgMc6q7jTlyueKsP203LHtd2W94R1YVSyqI7dLSqmrc2dP0n2ohvTwUUo4cbzMYOBtG5j7q8snVB4CFEOiUzXV28Umbqn8FkA3d3YwiCMfkZ00fpCTkDCy5u96g71bKYZk5asWvqmvPMa4w66hYn1Ow6tL"
            // key: "lT6tCspCxruCW1T1d90ynUrFuTSQFQKByA52NdYQHwszIDlOWtDGWRiKU61DwsEfAGTWEsz8y28RKdA56jY0uugVUKmD1YXUGsx5BVICqp5dcnVitlOxm9BdD9EzJ7KzybXEBh74nzzPzlBSb5qneNYFLAEWZRlSdZ4g1jC2YXGnlkUpvMBvuQsOla4FQjbGtX7VYo5V0IcW0rmDcu7ldWtrlnDoZe3VxAiRHXQwfxSFG0BbJGLretKPAeGsJzUZ"
          }
        );
        api_token = res.data.api_token;
        console.log(api_token);

        localStorage.setItem("api_token_shop", api_token);
      }
      api.defaults.params = { api_token: api_token };
    };
    const loadCart = async () => {
      if (!api_token) {
        await login();
      }
      const res = await post({
        route: "api/cart/products"
      });
      cart.value = res.data;
    };
    loadCart();

    const addToCart = async () => {
      if (!api_token) {
        await login();
      }
      const res = await post(
        {
          route: "api/cart/add"
        },
        {
