    };
    const customer = async () => {
      const res = await post(
        {
          route: "api/customer"
        },
        {
          firstname: "Dear",
          lastname: "Customer",
          email: "customer@example.com",
          telephone: "+1 879 2548022"
        }
      );
      console.log("shippingAddress", res.data);
    };

    const order = async () => {
